
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'

import { mapGetters, mapActions } from 'vuex'
import Checkbtn from '../checkbtn/checkbtn'
export default {
name: "pay",
props: {
title: {
type: String,
default: ''
},
hasmoney: {
type: Number,
default: 0
},
isChecked: {
type: Number,
default: 0
},
isChosed: {
type: Boolean,
default: false
},
type: {
type: Number,
default: 0
},
paytype: {
type: Number,
default: 0
},
moneyBox: {
type: Array,
default: []
},
moneyIndex: {
type: Number,
default: null
}
},
components: {
Checker,
CheckerItem,
Checkbtn,
XButton,
XSwitch
},
computed: {
...mapGetters([
'userInfo'
]),
},
created() {
this.myMoneyIndex = this.moneyIndex
this.isMyChecked = this.isChecked
this.isMyChosed = this.isChosed
},
watch: {
myMoneyIndex(newval) {
console.log(newval)
this.$emit('index-change', newval)
},
moneyIndex(newval) {
this.myMoneyIndex = newval
},
isMyChecked(newval) {
this.$emit('checked-change', newval)
},
isChecked(newval) {
this.isMyChecked = newval
},
isMyChosed(newval) {
this.$emit('isChosed-change', newval)
},
isChosed(newval) {
this.isMyChosed = newval
},
},
data() {
return {
myMoneyIndex: null,
isMyChecked: null,
isMyChosed: null,
radioRequired: true
}
},
methods: {
doOpacity(val) {
if (val) {
return 1
}
return 0
}
}
}



