import Scroll from '../../../base/scroll/scroll'
import Pay from '../../../base/pay/pay'
import { userInfoMixin } from '../../../assets/js/mixin'
import { mapGetters, mapActions } from 'vuex'
export default {
  name: "buyCoin",
  mixins: [userInfoMixin],
  components: {
    Scroll,
    Pay
  },
  filters: {
    tag(val) {
      if (!val) {
        return ''
      }
    }
  },
  data() {
    return {
      preventDefaultException: { tagName: /^(INPUT|TEXTAREA|BUTTON|SELECT|DIV|P)$/ },
      isClick: true,
      paytype: 2,
      type: 2,
      isChecked: 0,
      title: '',
      moneyBox: [
        {
          price: '50',
          type: '',
          info: '0.6',
          month: 1
        },
        {
          price: '96',
          oldPrice: '120',
          type: '',
          info: '0.6',
          month: 2
        },
        {
          price: '120',
          oldPrice: '200',
          type: '',
          info: '0.6',
          month: 3
        }
      ],
      moneyIndex: 0,
      chosed: true
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.$refs.scrollBox.refresh()
      this.canLoading = true
    })
  },
  updated() {
    this.$nextTick(() => {
      this.$refs.scrollBox.refresh()
      this.canLoading = true
    })
  },
  computed: {
    ataloCount: {
      get() {
        let ataloCount = 0
        let choosePrice = parseFloat(this.moneyBox[this.moneyIndex].price)
        ataloCount = choosePrice - (this.chosed ? this.hasmoney : 0)
        return ataloCount
      },
      set(val) {
        return val
      }
    },
    userSetInfo() {
      let userTime
      if (this.userInfo.vipEndtime) {
        userTime = this.userInfo.vipEndtime.slice(0, 10)
      }
      return {
        avatorUrl: this.userInfo.headPicId,
        userName: this.userInfo.username || this.userInfo.telephone,
        userState: (this.userInfo.isVip ? 1 : 0),
        userTime: userTime
      }
    },
    hasmoney: {
      get() {
        let has = 0
        if (this.userCoin.availableDeposit) {
          has = parseFloat(this.userCoin.availableDeposit).toFixed(2)
        }
        return has
      },
      set(val) {
        return val
      }
    }
  },
  methods: {
    choosePay(index) {
      // if (index === this.moneyIndex) {
      //   return false
      // }
      this.moneyIndex = index
    },
    isChosed(val) {
      this.chosed = val
    },
    buyPlus() {
      if(hasmoney===0){
        return false
      }
      this.$axios.post('/api/user/vipShipRenewal', {
        telephone: this.userInfo.telephone,
        months: this.moneyBox[this.moneyIndex].month,
      }).then((response) => {
        let data = response.data
        console.log(data)
        if (data.code !== "200") {
          this.$vux.alert.show({
            title: '',
            content: data.message,
          })
        } else {
          let _this = this
          this.$vux.toast.show({
            text: '',
            type: 'success',
            time: 1000,
            onHide() {
              _this.refreshUserInfo()
              _this.$router.push({ name: 'user' })
            }
          })
        }
      }).catch((error) => {
        this.$vux.alert.show({
          title: '',
          content: '',
        })
      })
    },
    refreshUserInfo() {
      this.$axios.post('/api/user/refresh', {
        buyerUserId: this.userInfo.buyerUserId,
      }).then((response) => {
        if (response.data.code === '200') {
          this.setUserInfo(response.data.data)
        } else {
          this.$vux.alert.show({
            title: '',
            content: '',
          })
        }
      }).catch((error) => {
        this.$vux.alert.show({
          title: '',
          content: '',
        })
      })
    },
    ...mapActions([
      'setUserInfo'
    ])
  }
}



